<template>
  <a-card :bordered="false">
    <a-table :columns="columns" :data-source="data" :row-key="record => record.id" :pagination="false">
      <template slot="name" slot-scope="text, record">
        <div key="name">
          <a-input
            v-if="record.editable"
            style="margin: -5px 0"
            :value="text"
            @change="e => handleChange(e.target.value, record.id)"
          />
          <template v-else>{{ text }}</template>
        </div>
      </template>
      <template slot="action" slot-scope="text, record, index">
        <div class="editable-row-operations">
          <span v-if="record.editable">
            <a @click="() => save(record)"></a>
            <a-popconfirm title="?" @confirm="() => cancel(record, index)">
              <a></a>
            </a-popconfirm>
          </span>
          <span v-else>
            <a @click="() => handleEdit(record)"></a>
            <a-popconfirm @confirm="() => handleDel(record.id)" @cancel="cancel" ok-text="" cancel-text="">
              <template slot="title">
                <p>?</p>
                <p></p>
              </template>
              <a></a>
            </a-popconfirm>
          </span>
        </div>
      </template>
    </a-table>
    <div class="operate">
      <a-button type="dashed" style="width: 100%" icon="plus" @click="handleAdd"></a-button>
    </div>
  </a-card>
</template>

<script>
import { getCategoryList, addCategory, updateCategory, delCategory } from '@/api/category'
export default {
  name: 'Category',
  props: {
    /**
     * : 1:, 2:, 3:, 4:
     * : 1
     */
    kind: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      columns: [
        {
          title: '',
          dataIndex: 'name',
          width: '50%',
          scopedSlots: { customRender: 'name' }
        },
        {
          title: '',
          key: 'action',
          scopedSlots: { customRender: 'action' }
        }
      ],
      old_data: [],
      data: []
    }
  },
  mounted() {
    this.getCategoryList()
  },
  methods: {
    getCategoryList() {
      getCategoryList({ kind: this.kind }).then(res => {
        this.data = res.data
        this.old_data = res.data
      })
    },
    handleChange(value, id) {
      const newData = [...this.data]
      const target = newData.filter(item => id === item.id)[0]
      if (target) {
        target['name'] = value
        this.data = newData
      }
    },
    handleAdd() {
      const category = {
        id: 0,
        name: '',
        editable: true
      }
      this.data.push(category)
    },
    handleEdit(row) {
      const newData = [...this.data]
      const target = newData.filter(item => row.id === item.id)[0]
      if (target) {
        target.editable = true
        this.data = newData
      }
    },
    handleDel(id) {
      delCategory(id).then(res => {
        if (res.code === 0) {
          this.getCategoryList()
        }
      })
    },
    save(row) {
      if (row.id === 0) {
        const params = {
          name: row.name,
          kind: this.kind
        }
        addCategory(params).then(res => {
          if (res.code === 0) {
            this.getCategoryList()
          }
        })
      } else {
        updateCategory(row.id, row).then(res => {
          if (res.code === 0) {
            this.getCategoryList()
          }
        })
      }
    },
    cancel(row, index) {
      if (row.id === 0) {
        this.data.splice(index, 1)
      } else {
        const newData = [...this.data]
        const target = newData.filter(item => row.id === item.id)[0]
        if (target) {
          Object.assign(target, this.old_data.filter(item => row.id === item.id)[0])
          delete target.editable
          this.data = newData
        }
      }
    }
  }
}
</script>

<style scoped>
.editable-row-operations a {
  margin-right: 8px;
}
</style>
