import { axios } from '@/utils/request'

// 
export function getCategoryList(params) {
  return axios({
    url: '/categories',
    method: 'get',
    params
  })
}

// 
export function addCategory(params) {
  return axios({
    url: '/categories',
    method: 'post',
    data: params
  })
}

// 
export function updateCategory(id, params) {
  return axios({
    url: `/categories/${id}`,
    method: 'put',
    data: params
  })
}

// 
export function delCategory(id) {
  return axios({
    url: `/categories/${id}`,
    method: 'delete'
  })
}
